interface DateInputInterface {
  onChangeDate?: (date: string) => void
  min?: string
  max?: string
  value?: string
  disabled: boolean
}

function DateInput({ onChangeDate, min, max, value, disabled = false }: DateInputInterface) {
  return (
    <ARECDate
      disabled={disabled}
      min={min}
      max={max}
      value={value}
      onChange={event => onChangeDate && onChangeDate(event.target.value)}
    />
  )
}

