interface GetCertInfoProps {
  certOwner: string
  beneficiary: string
  nameBeneficiary: string
  memoCertificate: string
  setCertOwner: (owner: string) => void
  setBeneficiary: (beneficiary: string) => void
  setNameBeneficiary: (nameBeneficiary: string) => void
  setMemoCertificate: (memoCertificate: string) => void
}

function ButtonUpOrDown({ bShow, setbShow }: { bShow: boolean; setbShow: (bShow: boolean) => void }) {
  return (
    <RowFixed>
      <ButtonEmpty
        style={{ padding: '4px' }}
        onClick={() => {
          setbShow(!bShow)
        }}
      >
        {bShow ? (
          <MinusCircle size="16px" style={{ margin: '0px 8px 0px 20px' }} />
        ) : (
          <PlusCircle size="16px" style={{ margin: '0px 8px 0px 20px' }} />
        )}
      </ButtonEmpty>
    </RowFixed>
  )
}

export function GetCertificateInfo({
  certOwner,
  beneficiary,
  nameBeneficiary,
  memoCertificate,
  setCertOwner,
  setBeneficiary,
  setNameBeneficiary,
  setMemoCertificate
}: GetCertInfoProps) {
  const theme = useContext(ThemeContext)

  const [showOwnerName, setShowOwnerName] = useState(false)
  const [showbeneficiary, setShowbeneficiary] = useState(false)
  const [showNameBeneficiary, setShowNameBeneficiary] = useState(false)
  const [showMemoCertificate, setShowMemoCertificate] = useState(true)

  return (
    <Container>
      <RowBetween align="center" height="20px" style={{ padding: '1rem 1rem 0.75rem 1rem' }}>
        <RowFixed>
          <TYPE.Body color={theme.text2} fontWeight={500} fontSize={16}>
            <strong>Retirement Badge Info:</strong>
          </TYPE.Body>
          <QuestionHelper
            bkgOff={true}
            small={'m'}
            info={<>This is the additional information items that will be included in the minted retirement badge.</>}
          />
        </RowFixed>
        <div />
      </RowBetween>
      <SeparatorBlack />
      <AutoColumn gap="4px" style={{ padding: '0.5rem' }}>
        <RowBetween>
          <RowFixed style={{ padding: '0.3rem 0.5rem 0rem' }}>
            <TYPE.Body color={theme.text2} fontWeight={500} fontSize={14}>
              Badge Owner Name (Optional):
            </TYPE.Body>
            <QuestionHelper
              bkgOff={true}
              small={'s'}
              info={<>This is the name of the owner of the retirement badge that will be minted.</>}
            />
          </RowFixed>
          <ButtonUpOrDown bShow={showOwnerName} setbShow={setShowOwnerName} />
        </RowBetween>
        {showOwnerName && (
          <MessageInputPanel value={certOwner} onUserInput={setCertOwner} placeholder={`Badge Owner Name`} />
        )}

        <RowBetween>
          <RowFixed style={{ padding: '0.3rem 0.5rem 0rem' }}>
            <TYPE.Body color={theme.text2} fontWeight={500} fontSize={14}>
              Beneficiary Account Address: (Optional):
            </TYPE.Body>
            <QuestionHelper
              bkgOff={true}
              small={'s'}
              info={<>This is the account address of the retirement beneficiary.</>}
            />
          </RowFixed>
          <ButtonUpOrDown bShow={showbeneficiary} setbShow={setShowbeneficiary} />
        </RowBetween>
        {showbeneficiary && (
          <AddressInputPanel
            id="recipient"
            simple={true}
            value={beneficiary !== ZERO_ADDRESS ? beneficiary : ''}
            onChange={setBeneficiary}
            placeholder={'Beneficiary Account Address'}
          />
        )}

        <RowBetween>
          <RowFixed style={{ padding: '0.3rem 0.5rem 0rem' }}>
            <TYPE.Body color={theme.text2} fontWeight={500} fontSize={14}>
              Beneficiary Name (Optional):
            </TYPE.Body>
            <QuestionHelper bkgOff={true} small={'s'} info={<>This is the name of the retirement beneficiary. </>} />
          </RowFixed>
          <ButtonUpOrDown bShow={showNameBeneficiary} setbShow={setShowNameBeneficiary} />
        </RowBetween>
        {showNameBeneficiary && (
          <MessageInputPanel
            value={nameBeneficiary}
            onUserInput={setNameBeneficiary}
            placeholder={`Retirement Beneficiary Name`}
          />
        )}

        <RowBetween>
          <RowFixed style={{ padding: '0.3rem 0.5rem 0rem' }}>
            <TYPE.Body color={theme.text2} fontWeight={500} fontSize={14}>
              Retirement Memo: (Optional):
            </TYPE.Body>
            <QuestionHelper
              bkgOff={true}
              small={'s'}
              info={<>This is the optional message that could be recorded in the retirement badge. </>}
            />
          </RowFixed>
          <ButtonUpOrDown bShow={showMemoCertificate} setbShow={setShowMemoCertificate} />
        </RowBetween>
        {showMemoCertificate && (
          <ResizingTextArea
            value={memoCertificate}
            onUserInput={setMemoCertificate}
            placeholder={`Retirement Badge Memo`}
            borderRadius={'6px'}
            small={true}
            fontSize="1rem"
          />
        )}
      </AutoColumn>
    </Container>
  )
}

export default function ARECIssuanceDate({
  startDate,
  endDate,
  minDate,
  maxDate,
  onChangeDate,
  id,
  children,
  active
}: ARECIssuanceDateProps) {
  const below540 = useMedia('(max-width: 540px)')

  const theme = useContext(ThemeContext)
  return (
    <InputPanel id={id}>
      <Container>
        {below540 ? (
          <>
            <RowBetween style={{ alignItems: 'center', padding: '0.2rem 0.2rem 0rem 0.6rem' }}>
              <TYPE.Body color={theme.text2} fontWeight={500} fontSize={16} width={'45%'}>
                <strong>Start AREC Date:</strong>
              </TYPE.Body>
              <DateInput value={startDate} disabled={true} />
            </RowBetween>
            <RowBetween style={{ alignItems: 'center', padding: '0rem 0.2rem 0.2rem 0.6rem' }}>
              <TYPE.Body color={theme.text2} fontWeight={500} fontSize={16} width={'45%'}>
                <strong>End AREC Date:</strong>
              </TYPE.Body>
              <DateInput value={endDate} onChangeDate={onChangeDate} max={maxDate} min={minDate} disabled={!active} />
            </RowBetween>
          </>
        ) : (
          <>
            <LabelRow>
              <RowBetween style={{ alignItems: 'center' }}>
                <TYPE.Body color={theme.text2} fontWeight={500} fontSize={16} width={'45%'}>
                  <strong>Start AREC Date:</strong>
                </TYPE.Body>
                <ArrowRight size={16} width={'10%'} />
                <TYPE.Body color={theme.text2} fontWeight={500} fontSize={16} width={'45%'} textAlign={'right'}>
                  <strong>End REC Date:</strong>
                </TYPE.Body>
              </RowBetween>
            </LabelRow>

            <DateRow style={{ padding: '0.2rem 0.2rem 0.2rem 0.2rem', height: '60px' }}>
              <DateInput value={startDate} disabled={true} />
              <div />
              <DateInput value={endDate} onChangeDate={onChangeDate} max={maxDate} min={minDate} disabled={!active} />
            </DateRow>
          </>
        )}

        <div style={{ padding: '0.2rem 0.6rem 0.2rem 0.6rem', marginBottom: '4px' }}>{children}</div>
      </Container>
    </InputPanel>
  )
}

