export function getPowerString(power: BigNumber): string {
  if (power.lt(BigNumber.from(1000_000_000))) {
    return new Fraction(power.toString(), JSBI.BigInt(1000_000)).toFixed(3, { groupSeparator: ',' }).concat(' KWH')
  } else {
    return new Fraction(power.toString(), JSBI.BigInt(1000_000_000)).toFixed(3, { groupSeparator: ',' }).concat(' MWH')
  }
}

export function DetailedARECInfo({ recData }: { recData: RECData }) {
  const theme = useContext(ThemeContext)
  //  const arkreenToken = useCurrency(arkreenTokenAddress)

  const startDate = DateTime.fromSeconds(recData.startTime) ?? ''
  const endDate = DateTime.fromSeconds(recData.endTime) ?? ''

  //  const powerAmount = arkreenToken ? tryParseAmount(recData.amountREC.toString(), arkreenToken) : undefined
  //  const powerAmount = new Fraction(recData.amountREC.toString(), JSBI.BigInt(1000000))
  //  const powerAmountString = (powerAmount?.toFixed(3, { groupSeparator: ',' }) ?? '0').concat(' KWH')

  const powerAmountString = getPowerString(recData.amountREC)

  const recStatus =
    recData?.status === REC_STATUS.Pending
      ? 'Pending'
      : recData?.status === REC_STATUS.Certified
      ? 'Certified'
      : recData?.status === REC_STATUS.Cancelled
      ? 'Cancelled'
      : recData?.status === REC_STATUS.Rejected
      ? 'Rejected'
      : ' '

  return (
    <ARECContainer>
      <RowBetween align="center" height="24px">
        <RowFixed>
          <Text fontWeight={500} fontSize={14} color={theme.text2}>
            AREC Status:
          </Text>
          <QuestionHelper
            bkgOff={true}
            small={'s'}
            info={<>This is the selected AREC Status. Only certified AREC can be redeemed or liquidized.</>}
          />
        </RowFixed>
        <Text
          lineHeight={'24px'}
          fontWeight={700}
          fontSize={14}
          color={recData?.status === REC_STATUS.Certified ? theme.text1 : theme.primaryText1}
        >
          {recStatus}
        </Text>
      </RowBetween>

      <RowBetween align="center" height="24px">
        <RowFixed>
          <Text fontWeight={500} fontSize={14} color={theme.text2}>
            AREC Issuer:
          </Text>
          <QuestionHelper bkgOff={true} small={'s'} info={<>This is the name of the entity issuing AREC NFT.</>} />
        </RowFixed>
        <Text lineHeight={'24px'} fontWeight={700} fontSize={14} color={theme.text1}>
          {shortenAddress(recData.issuer, 6)}
        </Text>
      </RowBetween>

      {recData.status === REC_STATUS.Certified && (
        <RowBetween align="center" height="24px">
          <RowFixed>
            <Text fontWeight={500} fontSize={14} color={theme.text2}>
              AREC Serial No:
            </Text>
            <QuestionHelper
              bkgOff={true}
              small={'s'}
              info={<>This is the unique serial number of the AREC certified by the issuer.</>}
            />
          </RowFixed>
          <Text lineHeight={'24px'} fontWeight={700} fontSize={14} color={theme.text1}>
            {recData.serialNumber}
          </Text>
        </RowBetween>
      )}

      <RowBetween align="center" height="24px">
        <RowFixed>
          <Text fontWeight={500} fontSize={14} color={theme.text2}>
            Earliest AREC Date:
          </Text>
          <QuestionHelper
            bkgOff={true}
            small={'s'}
            info={<>This is the earliest date when the renewable energy of the selected AREC is generated.</>}
          />
        </RowFixed>
        <Text lineHeight={'24px'} fontWeight={700} fontSize={14} color={theme.text1}>
          {startDate?.toFormat('yyyy-LLL-dd')}
        </Text>
      </RowBetween>

      <RowBetween align="center" height="24px">
        <RowFixed>
          <Text fontWeight={500} fontSize={14} color={theme.text2}>
            Latest AREC Date:
          </Text>
          <QuestionHelper
            bkgOff={true}
            small={'s'}
            info={<>This is the last date when the renewable energy of the selected AREC is generated.</>}
          />
        </RowFixed>
        <Text lineHeight={'24px'} fontWeight={700} fontSize={14} color={theme.text1}>
          {endDate?.toFormat('yyyy-LLL-dd')}
        </Text>
      </RowBetween>

      <RowBetween align="center" height="24px">
        <RowFixed>
          <Text fontWeight={500} fontSize={14} color={theme.text2}>
            Total RE Amount:
          </Text>
          <QuestionHelper
            bkgOff={true}
            small={'s'}
            info={<>This is the amount of the total renewable energy recorded in the selected AREC NFT.</>}
          />
        </RowFixed>
        <Text lineHeight={'24px'} fontWeight={700} fontSize={14} color={theme.text1}>
          {powerAmountString}
        </Text>
      </RowBetween>

      <RowBetween align="center" height="24px">
        <RowFixed>
          <Text fontWeight={500} fontSize={14} color={theme.text2}>
            AREC Region:
          </Text>
          <QuestionHelper bkgOff={true} small={'s'} info={<>This is the region of the selected AREC.</>} />
        </RowFixed>
        <Text lineHeight={'24px'} fontWeight={700} fontSize={14} color={theme.text1}>
          {recData.region}
        </Text>
      </RowBetween>

      <RowBetween align="center" height="24px">
        <RowFixed>
          <Text fontWeight={500} fontSize={14} color={theme.text2}>
            AREC cID:
          </Text>
          <QuestionHelper
            bkgOff={true}
            small={'s'}
            info={
              <>
                This is the cID of the renewable energy data in ipfs, with which the selected AREC RE amount can be
                verified.
              </>
            }
          />
        </RowFixed>
        <Text lineHeight={'24px'} fontWeight={700} fontSize={14} color={theme.text1}>
          {shortenCID(recData.cID)}
        </Text>
      </RowBetween>
    </ARECContainer>
  )
}

