export function feswType(chainId?: ChainId): string {
  if (
    chainId === ChainId.MAINNET ||
    chainId === ChainId.ROPSTEN ||
    chainId === ChainId.RINKEBY ||
    chainId === ChainId.GÃ–RLI ||
    chainId === ChainId.KOVAN
  )
    return 'FESW'
  return 'FESW-V2'
}

